<template>
  <div id="app">
    <div class="contenedor">
      <router-view/>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
}
</script>

<style>

.contenedor {
  margin-top: 48px;
}
* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  outline: none;
  text-transform: none;
  text-decoration: none;
}
*::-webkit-scrollbar{
  width: 5px;
}
*::-webkit-scrollbar-thumb{
  background: rgb(var(--uno));
  border-radius: 5px;
}
:root {
  --uno: 249, 103, 83;
  --dos: 5, 45, 73;
  --tres: 190, 190, 190;
}
body {
  overflow-x: hidden;
  width: 100%;
}



</style>



